import React, { Component, Fragment } from 'react';
import { Card, DatePicker, Select, Icon, Input, Form, Collapse } from 'antd';
import getCellColSpan, { strToObj } from '../../utils/CellUtil';
import formItemLayout from '../../configs/layout';
import { renderModalSelect } from '../../utils/RenderUtil';
import { TRUE } from '../../utils/Constants';

const { Option } = Select;
const Panel = Collapse.Panel;
const allowCallApi = ['import', 'export', 'edit', 'view', 'mult'];
const allowCallModal = ['add', 'edit', 'view'];

class BaseEdit extends Component {
    constructor(props) {
		super(props);
		this.state = {
            deleteComponent: false,
        };
        this.onDeleteComponent = this.onDeleteComponent.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.onAddListItem = this.onAddListItem.bind(this);
        this.onDeleteListItem = this.onDeleteListItem.bind(this);
        this.renderBtnPanelHeader = this.renderBtnPanelHeader.bind(this);
        this.onDeleteButton = this.onDeleteButton.bind(this);
    }
    getItemSetType() {
        return [];
    }
    componentWillReceiveProps(nextProps) {
		const { dropConfig } = nextProps;
        const { type, configs = {} } = dropConfig;
        const itemSetType = this.getItemSetType();
		if (~itemSetType.indexOf(type) && !this[`init${type}`] && nextProps.visible) {
			const lowerCaseType = type.toLowerCase();
			const stateKey = `${lowerCaseType}Arr`;
			const stateVal =  configs[stateKey] || [];
			this.setState({
				[stateKey]: stateVal,
            });
			this[`${lowerCaseType}Index`] = stateVal.length;
			this[`init${type}`] = true;
		}
	}
    handleSubmit(e) {
        e.preventDefault();
		e.stopPropagation();
		const { deleteComponent, buttonArr } = this.state;
        const { form, onUpdateConfigs, dropConfig, colSpanArr,
            formItemArr, inForm = true } = this.props;
        const itemSetType = this.getItemSetType();
		form.validateFields((err, values) => {
	        if (!err) {
				const newDropConfig = Object.assign({}, dropConfig, values);
				if (deleteComponent) {
					newDropConfig.deleteFlag = true;
				}
				newDropConfig.cellStyles = strToObj(values.cellStyles);
				const configType = newDropConfig.type;
				if (~itemSetType.indexOf(configType) && configType !== 'Button') {
					const lowerCaseType = configType.toLowerCase();
					const stateKey = `${lowerCaseType}Arr`;
					const newList = this.state[stateKey].slice();
					newList.forEach((item) => {
						const { index } = item;
						const kLabel = `${lowerCaseType}Label-${index}`;
						const kValue = `${lowerCaseType}Value-${index}`;
						item.label = values[kLabel];
						item.value= values[kValue];
						delete newDropConfig[kLabel];
						delete newDropConfig[kValue];
					});
					newDropConfig[stateKey] = newList;
				} else if (configType === 'DatePicker' || configType === 'RangePicker') {
					const { format } = values;
					newDropConfig.showTime = values.showTime === TRUE;
					if (configType === 'DatePicker') {
						newDropConfig.initialValue = values.initialValue && values.initialValue.format(format);
					}
				} else if (configType === 'Button') {
					const newButtonArr = buttonArr.slice();
					newButtonArr.forEach((item) => {
						const { btnIndex } = item;
						const btnText = `btnText-${btnIndex}`;
						const antdType = `antdType-${btnIndex}`;
						const htmlType = `htmlType-${btnIndex}`;
						const api = `api-${btnIndex}`;
						const expandFlag = `expandFlag-${btnIndex}`;
						const modalName = `modalName-${btnIndex}`;
						item.btnText = values[btnText];
						item.antdType= values[antdType];
						item.htmlType = values[htmlType];
						item.expandFlag = values[expandFlag];
						item.api = values[api];
						item.modalName = values[modalName];
						const expandCount = `expandCount-${btnIndex}`;
						if (values[expandFlag] === TRUE) {
							item.expandCount = values[expandCount];
						}
						delete newDropConfig[modalName];
						delete newDropConfig[expandCount];
						delete newDropConfig[expandFlag];
						delete newDropConfig[api];
						delete newDropConfig[htmlType];
						delete newDropConfig[btnText];
						delete newDropConfig[antdType];
					});
					newDropConfig.buttonArr = newButtonArr;
                }
                if (inForm) {
                    const { originSpan = 1 } = values;
					newDropConfig.originSpan = originSpan;
                    newDropConfig.colSpan = getCellColSpan(newDropConfig.colIndex, originSpan,
                        formItemArr, colSpanArr);
				}
				delete newDropConfig.configs;
                this.onCloseEdit();
				onUpdateConfigs(newDropConfig);
	        }
	      },
	    );
    }
    onCloseEdit() {
        this.initBtn = false;
        const itemSetType = this.getItemSetType();
        itemSetType.forEach((item) => {
            this[`init${item}`] = false;
        });
        this.setState({
			deleteComponent: false,
        });
    }
    onDeleteComponent() {
        this.setState({
			deleteComponent: true,
        });
    }
    onDeleteButton(btnIndex) {
		const { buttonArr = [] } = this.state;
		const newButtonArr = buttonArr.reduce((arr, item) => {
			if (item.btnIndex !== btnIndex) {
				arr.push(item);
			}
			return arr;
		}, []);
		this.setState({
			buttonArr: newButtonArr,
		});
	}
    onDeleteListItem(index, key) {
		const stateKey = `${key}Arr`;
		const list = this.state[stateKey];
		const newList = list.reduce((arr, item) => {
			if (item.index !== index) {
				arr.push(item);
			}
			return arr;
		}, []);
		this.setState({
			[stateKey]: newList,
		});
    }
    onAddListItem(key) {
		const stateKey = `${key}Arr`;
		const list = this.state[stateKey];
		const newList = list.slice();
		newList.push({
			label: '',
			value: '',
			index: this[`${key}Index`],
		});
		this.setState({
			[stateKey]: newList,
		});
		this[`${key}Index`] += 1;
    }
    renderDefaultValueItem() {
		const { dropConfig, form } = this.props;
		const { type, initialValue } = dropConfig;
        const { getFieldDecorator } = form;
        const itemSetType = this.getItemSetType();
		if (~itemSetType.indexOf(type)) {
			const lowerCaseType = type.toLowerCase();
			const stateKey = `${lowerCaseType}Arr`;
			const list = this.state[stateKey] || [];
			return (
				<Form.Item {...formItemLayout} label="initialValue">
					{getFieldDecorator('initialValue', {
						initialValue,
					})(
						<Select allowClear>
						{
							list.map((item) => {
								const { index } = item;
								const optionValue = form.getFieldValue(`${lowerCaseType}Value-${index}`);
								const optionText = form.getFieldValue(`${lowerCaseType}Label-${index}`);
								return (
									<Option value={optionValue} key={`op-${index}`}>
										{`${optionValue}/${optionText}`}
									</Option>
								);
							})
						}
					</Select>
					)}
				</Form.Item>
			);
		} else if (type === 'DatePicker') {
			return (<Form.Item {...formItemLayout} label="initialValue">
				{getFieldDecorator('initialValue', {
					initialValue,
				})(
					<DatePicker style={{ width: '100%' }} />
				)}
			</Form.Item>);
		} else if (type === 'Input') {
			return (<Form.Item {...formItemLayout} label="initialValue">
					{getFieldDecorator('initialValue', {
						initialValue,
					})(
						<Input />
					)}
				</Form.Item>);
		} else if (type === 'Text') {
			return (<Form.Item {...formItemLayout} label="text">
					{getFieldDecorator('text', {
						initialValue: dropConfig.text,
					})(
						<Input />
					)}
				</Form.Item>);
		}  else {
			return null;
		}
	}
    renderBtnPanelHeader({ btnText, btnIndex }) {
        return (
            <div className="panel-header">
                <span className="title">{btnText}</span>
                <Icon
                    type="close"
                    className="btn-delete"
                    onClick={() => {
                        this.onDeleteButton(btnIndex);
                    }}
                />
            </div>
        );
    }
    renderTabs() {
		return this.renderItemSettingCard({ title: 'tab', dataKey:'tabs' });
	}
	renderBreadcrumb() {
		return this.renderItemSettingCard({
			title: 'breadcrumb',
			dataKey: 'breadcrumb',
			labPlaceholder: '',
			valPlaceholder: '',
		});
	}
    renderSelect() {
		return this.renderItemSettingCard({ title: 'select', dataKey:'select' });
	}
	renderRadio() {
		return this.renderItemSettingCard({ title: 'radio', dataKey:'radio' });
	}
	renderCheckbox() {
		return this.renderItemSettingCard({ title: 'checkbox', dataKey:'checkbox' });
	}
    renderItemSettingCard({ title, dataKey, labPlaceholder = 'label', valPlaceholder = 'label' }) {
		const { form } = this.props;
		const stateKey = `${dataKey}Arr`;
        const itemList = this.state[stateKey] || [];
		return (
			<Card
				title={title}
				extra={<Icon type="plus-circle" onClick={() => {
					this.onAddListItem(dataKey);
				}} />}
			>
				{
					itemList.map(({ label, value, index }) => {
						return (
							<Fragment key={`${dataKey}-${index}`}>
								<Form.Item
									key={`${dataKey}-label-${index}`}
									style={{ width: '45%', display: 'inline-block' }} 
								>
									{form.getFieldDecorator(`${dataKey}Label-${index}`, {
										initialValue: label,
										rules: [{ required: true, message: labPlaceholder }]
									})(
										<Input placeholder={labPlaceholder} />
									)}
								</Form.Item>
								<Form.Item
									key={`${dataKey}-value-${index}`}
									style={{ width: '45%', display: 'inline-block', marginLeft: '10px' }} 
								>
									{form.getFieldDecorator(`${dataKey}Value-${index}`, {
										initialValue: value,
									})(
										<Input placeholder={valPlaceholder} />
									)}
								</Form.Item>
								<Icon
									type="close-circle"
									style={{ marginLeft: '6px', lineHeight: '39px' }}
									onClick={() => {
										this.onDeleteListItem(index, dataKey);
									}}
								/>
							</Fragment>
						);
					})
				}
			</Card>
		);
	}
	renderBtnExpand(btnConfig) {
		const { form } = this.props;
		const { expandFlag = '0' } = this.state;
		const { index, expandCount } = btnConfig;
		return (
			<Fragment>
				<Form.Item
					key={`expandFlag-${index}`}
					label=""
					{...formItemLayout}
				>
					{form.getFieldDecorator(`expandFlag-${index}`, {
						initialValue: expandFlag,
					})(
						<Select>
							<Option value="0"></Option>
							<Option value="1"></Option>
						</Select>
					)}
				</Form.Item>
				{
					form.getFieldValue(`expandFlag-${index}`) === '1' ?
						(<Form.Item
							key={`expandCount-${index}`}
							label=""
							{...formItemLayout}
						>
							{form.getFieldDecorator(`expandCount-${index}`, {
								initialValue: expandCount,
							})(
								<Input placeholder="expandCount" />
							)}
						</Form.Item>) : null
				}
			</Fragment>
		);
	}
	renderApiItem({ btnIndex, api, logicType }) {
		const { layerConfig = {}, form } = this.props;
		// api
		if (allowCallApi.indexOf(logicType) >= 0) {
			return renderModalSelect(layerConfig, form, api, btnIndex);
		}
		return null;
	}
	renderModalItem({ btnIndex, modalName, logicType }) {
		const { layerConfig = {}, form } = this.props;
		// modal
		if (allowCallModal.indexOf(logicType) >= 0) {
			return renderModalSelect(layerConfig, form, modalName, btnIndex);
		}
		return null;
	}
	renderBtnCard() {
		const { form } = this.props;
		const { buttonArr = [] } = this.state;
		return (
			<Card title="">
				<Collapse>
				{
					buttonArr.map((item) => {
						const { btnIndex, btnText, antdType = '', htmlType = 'button', logicType, textDisabled } = item;
						return (
							<Panel header={this.renderBtnPanelHeader(item)} key={`btn-${btnIndex}`}>
								<Form.Item
									key={`btnText-${btnIndex}`}
									label=""
									{...formItemLayout}
								>
									{form.getFieldDecorator(`btnText-${btnIndex}`, {
										initialValue: btnText,
										rules: [{ required: true, message: '' }],
									})(
										<Input placeholder="" disabled={textDisabled} />
									)}
								</Form.Item>
								<Form.Item
									key={`antdType-${btnIndex}`}
									label="type"
									{...formItemLayout}
								>
									{form.getFieldDecorator(`antdType-${btnIndex}`, {
										initialValue: antdType,
									})(
										<Select placeholder="" disabled={htmlType === 'submit' || htmlType === 'reset'}>
											<Option value="primary">primary</Option>
											<Option value="dashed">dashed</Option>
											<Option value="danger">danger</Option>
											<Option value="">none</Option>
										</Select>
									)}
								</Form.Item>
								<Form.Item
									key={`htmlType-${btnIndex}`}
									label="htmlType"
									{...formItemLayout}
								>
									{form.getFieldDecorator(`htmlType-${btnIndex}`, {
										initialValue: htmlType,
										rules: [{ required: true, message: 'htmlType' }]
									})(
										<Select disabled={htmlType === 'submit' || htmlType === 'reset'}>
											<Option value="submit">submit</Option>
											<Option value="reset">reset</Option>
											<Option value="button">button</Option>
										</Select>
									)}
								</Form.Item>
								{
									this.renderApiItem(item)
								}
								{
									this.renderModalItem(item)
								}
								{
									logicType === 'expand' ? this.renderBtnExpand(item) : null
								}
								
							</Panel>
						);
					})
				}
				</Collapse>
			</Card>
		);
	}
}

export default BaseEdit;