import React, { PureComponent } from 'react';
import { Modal, Layout } from 'antd';
import DragTabs from '../../DragTabs';
import MainContainer from '../MainContainer';

import './index.css';

const { Sider, Content } = Layout;

class ModalContainer extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            layoutConfig: [],
        };
        this.onSubmit = this.onSubmit.bind(this);
        this.onUpdateLayoutConfig = this.onUpdateLayoutConfig.bind(this);
    }
    onSubmit() {
        const { onOk, editModal } = this.props;
        const { layoutConfig } = this.state;
        onOk({
            ...editModal,
            layoutConfig,
        });
    }
    onUpdateLayoutConfig(layoutConfig) {
        this.setState({
            layoutConfig,
        })
    }
    render() {
        const { visible, onCancel, layerConfig } = this.props;
        return (
            <Modal
                title=""
                width={1024}
                visible={visible}
                maskClosable={false}
                onOk={this.onSubmit}
                onCancel={onCancel}
            >
                <Layout>
                        <Sider width="300">
                            <DragTabs />
                        </Sider>
                        <Layout>
                            <Content>
                                <MainContainer
                                    onUpdateLayoutConfig={this.onUpdateLayoutConfig}
                                    layerConfig={layerConfig}
                                    noLayer
                                />
                            </Content>
                        </Layout>
                </Layout>
            </Modal>
        );
    }
}

export default ModalContainer;