const initDropData = (dragData, context, isFormItem = false) => {
    const { dropComponent, dropConfig } = context.state;
    const { type, ...defaultConfigs } = dragData;
    let newDropConfig = {
        textAlign: 'left',
        configs: {},
    };
    if (dropComponent) {
        if (type === 'Button' && dropConfig.type === 'Button') {
            newDropConfig = Object.assign({}, dropConfig);
            newDropConfig.configs.buttonArr.push({
                btnIndex: context.btnIndex,
                ...defaultConfigs,
            });
            context.btnIndex += 1;
        } else {
            return;
        }
    } else {
        if (type === 'Button') {
            newDropConfig.configs.buttonArr = [{
                btnIndex: context.btnIndex,
                ...defaultConfigs,
            }];
            context.btnIndex += 1;
        } else {
            newDropConfig = {
                configs: {...defaultConfigs},
            };
        }
        newDropConfig.type = type;
    }
    if (isFormItem && newDropConfig.type !== 'Button') {
        newDropConfig.originSpan = context.props.originSpan;
        newDropConfig.cellStyles = context.props.cellStyles;
        newDropConfig.colIndex = context.props.colIndex;
        newDropConfig.colSpan = context.props.colSpan;
        newDropConfig.configs.label = type;
        newDropConfig.configs.name = `name-${context.props.colIndex}`;
    }
    return newDropConfig;
};

export default initDropData;