import React, { Component } from 'react';
import { Button, Form, Input, Modal } from 'antd';
import './index.css';

class PageNameModal extends Component {
    constructor(props) {
        super(props);
        this.onSubmit = this.onSubmit.bind(this);
    }
    onSubmit(e) {
        e && e.preventDefault();
        const { form, onOkModal } = this.props;
        form.validateFields((err, values) => {
            if (!err) {
                const { pageName, projectDir } = values;
                onOkModal(pageName, projectDir);
            }
        });
    }
    render() {
        const { visible, form, onCloseModal } = this.props;
        const { getFieldDecorator } = form;
        return (
            <Modal
                visible={visible}
                footer={null}
                width={450}
                closable
                centered
                onOk={this.onSubmit}
                onCancel={onCloseModal}
                title=""
                maskClosable
            >
                <Form
                    onSubmit={this.onSubmit}
                    style={{ padding: '20px 40px' }}
                >
                    <Form.Item>
                        {getFieldDecorator('pageName', {
                            rules: [
                                { required: true, message: '!' },
                                { pattern: /^[a-zA-Z]+$/, message: '' }
                            ],
                        })(
                            <Input placeholder="" />
                        )}
                    </Form.Item>
                    <Form.Item>
                        {getFieldDecorator('projectDir', {
                            rules: [
                                { required: true, message: '!' },
                            ],
                        })(
                            <Input placeholder="" />
                        )}
                    </Form.Item>
                    <Form.Item  style={{ marginTop: '15px', textAlign: 'center' }}>
                        <Button
                            type="primary"
                            htmlType="submit"
                        >
                            
                        </Button>
                    </Form.Item>
                </Form>
            </Modal>
        );
    }
}

export default Form.create()(PageNameModal);