import React, { Component } from 'react';
import { Button, Form, Input, Select, Drawer } from 'antd';
import  { objToStr, strToObj } from '../../../utils/CellUtil';
import { HEADER_CONTINAER, FALSE } from '../../../utils/Constants';
import formItemLayout from '../../../configs/layout';
import './index.css';

const TextArea = Input.TextArea;
const Option = Select.Option;

class BoxEdit extends Component {
    constructor(props) {
        super(props);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    handleSubmit(e) {
        e.preventDefault();
        const { form, onClose } = this.props;
        form.validateFields((err, values) => {
            if (!err) {
                const { boxStyles, ...rest } = values;
                const configs = {
                    boxStyles: strToObj(boxStyles),
                    ...rest,
                };
                onClose(configs);
            }
        });
    }
    render() {
        const { visible, form, onClose, configs = {}, onDeleteContainer, type } = this.props;
        const { boxStyles = {}, layoutColumn = 3, positionFixed = FALSE } = configs;
        const initialBoxStyles = objToStr(boxStyles);
        const { getFieldDecorator } = form;
        return (
            <Drawer
                visible={visible}
                maskClosable={false}
                width={420}
                title=""
                onClose={() => {
                    onClose(0);
                }}
            >
                <Form
                    onSubmit={this.handleSubmit}
                    style={{ padding: '0 20px' }}
                >
                    <Form.Item
                        label="style"
                        {...formItemLayout}
                    >
						{form.getFieldDecorator("boxStyles", {
							initialValue: initialBoxStyles,
						})(
							<TextArea placeholder="" />
						)}
					</Form.Item>
                    {
                        type === HEADER_CONTINAER ? (
                            <Form.Item
                            label=""
                            {...formItemLayout}
                        >
                            {getFieldDecorator('positionFixed', {
                                initialValue: positionFixed,
    
                            })(
                                <Select>
                                    <Option value="1"></Option>
                                    <Option value="0"></Option>
                                </Select>
                            )}
                        </Form.Item>
                        ) : null
                    }
                    <Form.Item
                        label=""
                        {...formItemLayout}
                    >
                        {getFieldDecorator('layoutColumn', {
                            initialValue: layoutColumn,
                            rules: [
                                { required: true, message: '!' },
                            ],
                        })(
                            <Select>
                                <Option value="1">1</Option>
                                <Option value="2">2</Option>
                                <Option value="3">3</Option>
                                <Option value="4">4</Option>
                                <Option value="5">5</Option>
                                <Option value="6">6</Option>
                                <Option value="7">7</Option>
                                <Option value="8">8</Option>
                            </Select>
                        )}
                    </Form.Item>
                    <div className="btn-box">
                        <Button
                            type="primary"
                            htmlType="submit"
                            style={{ marginRight: '10px' }}
                        >
                            
                        </Button>
                        <Button onClick={onDeleteContainer}></Button>
                    </div>
                </Form>
            </Drawer>
        );
    }
}

export default Form.create()(BoxEdit);