import React, { Fragment } from 'react';
import { Button, Drawer, Select, Input, Form } from 'antd';
import BaseEdit from '../../Core/BaseEdit';
import { objToStr } from '../../../utils/CellUtil';
import formItemLayout from '../../../configs/layout';
import './index.css';

const Option = Select.Option;
const { TextArea } = Input;

class CompEdit extends BaseEdit {
	getItemSetType() {
		return ['Tabs', 'Breadcrumb', 'Button', 'Radio'];
	}
	renderContent() {
		const { form, dropConfig } = this.props;
		const { placeholder, type } = dropConfig;
		const { getFieldDecorator } = form;
		if (this.state.deleteComponent) {
			return null;
		}
		if (type === 'Button') {
			return this.renderBtnCard();
		}
		if (type === 'Breadcrumb') {
			return this.renderBreadcrumb();
		}
		if (type === 'Tabs') {
			return this.renderTabs();
		}
		if (type === 'Radio') {
			return this.renderRadio();
		}
		return (
			<Fragment>
				{this.renderDefaultValueItem()}
				{
					type !== 'Text' && type !== 'Breadcrumb' ?
						(<Form.Item {...formItemLayout} label="placeholder">
							{getFieldDecorator('placeholder', {
								initialValue: placeholder,
							})(
								<Input />
							)}
						</Form.Item>) : null
				}
			</Fragment>
		);
	}
	render() {
		const { visible, onClose, form, dropConfig } = this.props;
		const initialCellStyles = objToStr(dropConfig.cellStyles);
		const { type, radioType, textAlign = 'center' } = dropConfig;
		return (
			<Drawer
				visible={visible}
				maskClosable={false}
				title=""
				onClose={() => {
					this.onCloseEdit();
					onClose();
				}}
				width={420}
			>
				<Form onSubmit={this.handleSubmit}>
					<Form.Item label="style" {...formItemLayout}>
						{form.getFieldDecorator("cellStyles", {
							initialValue: initialCellStyles,
						})(
							<TextArea placeholder="" />
						)}
					</Form.Item>
					{
						type === 'Button' ? (
							<Form.Item label="" {...formItemLayout}>
								{form.getFieldDecorator("textAlign", {
									initialValue: textAlign,
								})(
									<Select>
										<Option
											key="left"
											value="left"
										>
											
										</Option>
										<Option
											key="center"
											value="center"
										>
											
										</Option>
										<Option
											key="right"
											value="right"
										>
											
										</Option>
									</Select>
								)}
							</Form.Item>
						) : null
					}
					{
						type === 'Radio' ? (
							<Form.Item label="" {...formItemLayout}>
								{form.getFieldDecorator("radioType", {
									initialValue: radioType,
								})(
									<Select>
										<Option
											key="default"
											value="default"
										>
											
										</Option>
										<Option
											key="tab"
											value="tab"
										>
											tab
										</Option>
								</Select>
								)}
							</Form.Item>
						) : null
					}
                    {
                        this.renderContent()
                    }
                    <div className="btn-box">
						<Button onClick={this.onDeleteComponent}>
							
						</Button>
					</div>
			        <div className="btn-bottom-box">
						<Button type="primary" htmlType="submit">
							
						</Button>
			        </div>
		    	</Form>
	    	</Drawer>
	    );
	}
}

export default Form.create()(CompEdit);