import React, { PureComponent } from 'react';
import { Icon } from 'antd';
import DropContainer from '../DropContainer';
import BoxEdit from '../../EditDrawer/BoxEdit';
import { TRUE } from '../../../utils/Constants';
import './index.css';

class HeaderContainer extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            visible: false,
            configs: props.preview ? props.configs : {
                cellsArr: [{
                    dropIndex: 0,
                }],
                layoutColumn: 1, // 
            },
        };
        this.onToggleEdit = this.onToggleEdit.bind(this);
        this.onDeleteContainer = this.onDeleteContainer.bind(this);
        this.onUpdateConfigs = this.onUpdateConfigs.bind(this);
    }
    onToggleEdit() {
        this.setState({
            visible: !this.state.visible,
        });
    }
    onDeleteContainer() {
        const { onUpdateLayoutConfig, layoutIndex } = this.props;
        onUpdateLayoutConfig({ layoutIndex, deleteFlag: true });
        this.onToggleEdit();
    }
    onUpdateConfigs({ deleteFlag = false, ...values }) {
        const { configs } = this.state;
        const { onUpdateLayoutConfig, layoutIndex, layoutColumn } = this.props;
        const newConfigs = Object.assign({}, configs, layoutColumn);
        const newCellsArr = newConfigs.cellsArr.reduce((arr, item) => {
            // 
            if (deleteFlag) {
                if (values.dropIndex !== item.dropIndex) {
                    arr.push(Object.assign({}, item));
                } else {
                    arr.push({
                        dropIndex: values.dropIndex,
                    });
                }
            } else {
                if (values.dropIndex === item.dropIndex) {
                    arr.push(Object.assign({}, item, values));
                } else {
                    arr.push(Object.assign({}, item));
                }
            }
            return arr;
        }, []);
        newConfigs.cellsArr = newCellsArr;
        this.setState({
            configs: newConfigs,
        }, () => {
            onUpdateLayoutConfig({ layoutIndex, configs: newConfigs });
        });
    }
    rendeCell(layoutColumn) {
        if (!layoutColumn) {
            return null;
        }
        const componentArr = [];
        const width = `${100 / layoutColumn}%`;
        const { configs } = this.state;
        const { preview } = this.props;
        const { cellsArr = [] } = configs;
        const cellNum = cellsArr.length;
        for (let i = 0; i < cellNum; i++) {
            componentArr.push((
               <div className="cell border-box" key={`cell-${layoutColumn}-${i}`} style={{ width }}>
                    < DropContainer
                        dropIndex={i}
                        preview={preview}
                        dropConfig={cellsArr[i]}
                        onUpdateLayoutConfig={this.onUpdateConfigs}
                    />
               </div>
            ));
        }
        return componentArr;
    }
    render() {
        const { visible } = this.state;
        const { onUpdateLayoutConfig, layoutIndex, configs = {}, preview = false, type } = this.props;
        const { layoutColumn = 1, boxStyles, positionFixed } = configs;
        const positionCls = positionFixed === TRUE ? 'position-fixed' : '';
        const previewCls = preview ? 'preview-box' : '';
        return (
            <div className={`header-box fulu-cell-box ${positionCls}  ${previewCls}`} style={boxStyles}>
                {
                    this.rendeCell(+layoutColumn)
                }
                <BoxEdit
                    visible={visible}
                    configs={configs}
                    type={type}
                    onClose={(configs) => {
                        if (configs) {
                            const layoutConfig = {
                                layoutIndex,
                            };
                            const { layoutColumn } = configs;
                            const { cellsArr } = this.state.configs;
                            const newCellsArr = cellsArr.slice(0, layoutColumn);
                            for (let i = 0; i < layoutColumn; i++) {
                                if (newCellsArr.length <= i) {
                                    newCellsArr.push({
                                        dropIndex: i
                                    });
                                }
                            }
                            const newConfigs = {
                                ...configs,
                                cellsArr: newCellsArr,
                            };
                            layoutConfig.configs = newConfigs;
                            this.setState({
                                configs: newConfigs,
                            });
                            onUpdateLayoutConfig(layoutConfig);
                        }
                        this.onToggleEdit();
                    }}
                    onDeleteContainer={this.onDeleteContainer}
                />
                {
                    !preview ? 
                        <Icon type="form" className="icon-operation" onClick={this.onToggleEdit} /> : null
                }
            </div>
        );
    }
}

export default HeaderContainer;