import React, { PureComponent } from 'react';
import renderComponent from '../../../utils/RenderUtil';

class DropRender extends PureComponent {
	render() {
		const { dropConfig = {}, ...restProps } =this.props;
		const { cellStyles, textAlign = 'center', type } = dropConfig;
		const styleObj = type === 'Button' ? Object.assign({}, cellStyles, { textAlign }) : cellStyles;
		return (
			<div style={styleObj}>
                {
                    renderComponent(dropConfig, restProps)
                }
		    </div>
	    );
	}
}

export default DropRender;