import React, { Fragment } from 'react';
import { Button, Checkbox, Input, Icon, Tabs, Select, DatePicker, Breadcrumb, Radio, Form } from 'antd';
import FormContainer from '../component/Container/FormContainer';
import LineContainer from '../component/Container/LineContainer';
import TableContainer from '../component/Container/TableContainer';
import BoxContainer from '../component/Container/BoxContainer';
import HeaderContainer from '../component/Container/HeaderContainer';
import { TRUE } from './Constants';
import formItemLayout from '../configs/layout';
import { isIndex } from './common';

const CheckboxGroup = Checkbox.Group;
const { Option } = Select;
const { RangePicker } = DatePicker;
const { TabPane } = Tabs;
const { TextArea } = Input;

const renderComponent = (renderConfig, props) => {
    const { placeholder = '', type = 'Input' } = renderConfig;
    let component = null;
    switch(type) {
        case 'Text':
            component = renderText(renderConfig);
            break;
        case 'Breadcrumb':
            component = renderBreadcrumb(renderConfig);
            break;
        case 'Select':
            component = renderSelect(renderConfig);
            break;
        case 'RangePicker':
            component = renderRangePicker(renderConfig);
            break;
        case 'DatePicker':
            component = renderDatePicker(renderConfig);
            break;
        case 'Radio':
            component = renderRadio(renderConfig);
            break;
        case 'Checkbox':
            component = renderCheckbox(renderConfig);
            break;
        case 'Tabs':
            component = renderTabs(renderConfig);
            break;
        case 'Button':
            component = renderBtn(renderConfig, props);
            break;
        case 'FormContainer':
            component = renderForm(renderConfig, props);
            break;
        case 'LineContainer':
            component = renderLine(renderConfig, props);
            break;
        case 'TableContainer':
            component = renderTable(renderConfig, props);
            break;
        case 'BoxContainer':
            component = renderBox(renderConfig, props);
            break;
        case 'HeaderContainer':
            component = renderHeader(renderConfig, props);
            break;
        case 'TextArea':
            component = renderTextArea(renderConfig);
            break;
        case 'Input':
            component = (<Input placeholder={placeholder} />);
            break;
        default:
            break;
    }
    return component;
};

const renderTextArea = (configs) => {
    const { colIndex, dropIndex, originSpan, cellStyles, hasLinkage,  ...rest } = configs;
    return (<TextArea {...rest} />);
};

const renderForm = (configs, props) => {
    return (<FormContainer {...configs} {...props} />);
};

const renderHeader = (configs) => {
    return (<HeaderContainer {...configs} />);
};

const renderTable = (configs, props) => {
    return (<TableContainer {...configs} {...props} />);
};

const renderLine = (configs) => {
    return (<LineContainer {...configs} />)
};

const renderBox = (configs, props) => {
    return (<BoxContainer {...configs} {...props} />);
};

const renderBreadcrumb = ({ configs }) => {
    const { breadcrumbArr = [], style } = configs;
    return (
        <Breadcrumb style={style}>
            {
                breadcrumbArr.map(({ label, value }, i) => {
                    return (
                        <Breadcrumb.Item key={`breadcrumb-${i}`}>
                            { value ? 
                                <a href={value}>{label}</a> : <span>{label}</span>
                            }
                        </Breadcrumb.Item>
                    );
                })
            }
        </Breadcrumb>
    )
};
const renderText = ({ configs }) => {
    const { text, style } = configs;
    return (
        <span style={style}>{text}</span>
    );
};
const renderRadio = ({ configs }) => {
    const { style = {}, radioArr = [], radioType } = configs;
    const RadioItem = radioType === 'tab' ? Radio.Button : Radio;
    return (
        <Radio.Group style={{ ...style }}>
            {
                Array.isArray(radioArr) ? radioArr.map(({ key, label, value }) => {
                    return (
                        <RadioItem
                            key={key || value}
                            value={value}
                        >
                            {label}
                        </RadioItem>
                    );
                }) : null
            }
        </Radio.Group>
    );
};
const renderTabs = ({ configs }) => {
    const { style = {}, tabsArr = [] } = configs;
    return (
        <Tabs style={style}>
            {
                Array.isArray(tabsArr) ? tabsArr.map(({ key, label, value }) => {
                    return (
                        <TabPane
                            key={key || value}
                            tab={label}
                        >

                        </TabPane>
                    );
                }) : null
            }
        </Tabs>
   );
};
const renderCheckbox = ({ configs }) => {
    const { style = {}, checkboxArr = [] } = configs;
    if (checkboxArr.length > 0) {
        return (
            <CheckboxGroup options={checkboxArr} style={{ width: "100%", ...style }} />
        );
    }
    return null;
};
const renderSelect = ({ configs }) => {
    const { style = {}, options, selectArr, ...rest } = configs;
    const finalOptions = Object.hasOwnProperty.call(configs, 'options') ? configs.options : configs.selectArr;
    return (
        <Select style={{ width: '100%', ...style }} {...rest}>
            {
                Array.isArray(finalOptions) ? finalOptions.map(({ key, label, value }) => {
                    return (
                        <Option
                            key={key || value}
                            value={value}
                        >
                            {label}
                        </Option>
                    );
                }) : null
            }
        </Select>
    );
};
const renderDatePicker = ({ configs }) => {
    const { showTime, format } = configs;
        return (
            <DatePicker
                style={{ width: '100%' }}
                showTime={showTime}
                format={format}
            />
        );
};
const renderRangePicker = ({ configs }) => {
    const { showTime, format } = configs;
        return (
            <RangePicker
                showTime={showTime}
                format={format}
                style={{ width: '100%' }}
            />
        );
}

const renderBtn = ({ configs }, { context }) => {
    const { buttonArr = [] } = configs;
    const { expand, onCollapse } = context || {};
    return (
        <span className="btn-group">
            {
                buttonArr.map((btnConfig) => {
                    const { logicType, btnText, expandFlag = '0', expandCount, api, modalName, antdType,
                        style = {}, textDisabled, btnIndex, ...btnProps } = btnConfig;
                    if (expandFlag === TRUE) {
                        const collapseBtnText = expand ? '' : '';
                        return (
                            <span
                                className="btn-collapse"
                                onClick={onCollapse}
                                style={style}
                                key={`btn-${btnIndex}`}
                            >
                                {collapseBtnText}
                                <Icon type={expand ? 'up' : 'down'} />
                            </span>
                            );
                    }
                    if (btnConfig.htmlType !== 'submit' && btnConfig.htmlType !== 'reset') {
                        btnProps.onClick = (e) => {
                            if (context) {
                                if (modalName) {
                                    context.onShowModal(e, modalName, api);
                                }
                            }
                        };
                    }
                    return (
                        <Button
                            key={`btn-${btnIndex}`}
                            style={style}
                            type={antdType}
                            {...btnProps}
                        >
                            {btnText}
                        </Button>
                    );
                })
            }
        </span>
    );
}

const renderExpandBtn = (config, expand, onCollapse) => {
    const { btnArr } = config;
        return (
            <Fragment>
                {
                    btnArr.map((btn) => {
                        const { btnText = 'button', type, index, props,
                            style = {}, expandFlag = '0' } = btn;
                        if (expandFlag === '1') {
                            const collapseBtnText = expand ? '' : '';
                            return (
                                <a
                                    className="btn-collapse"
                                    onClick={onCollapse}
                                    href="javascript:void(0);"
                                    style={style}
                                    key={`btn-${index}`}
                                >
                                    {collapseBtnText}
                                    <Icon type={expand ? 'up' : 'down'} />
                                </a>
                                );
                        }
                        return (
                            <Button
                                key={`btn-${index}`}
                                type={type}
                                style={style}
                                {...props}
                            >
                                {btnText}
                            </Button>
                        );
                    })
                }
            </Fragment>
        );
};

const renderConfigForm = (form, renderConfig, initData = {}) => {
    const { getFieldDecorator } = form;
    const hasIndex = isIndex(initData.index);
    if (Array.isArray(renderConfig)) {
        return renderConfig.map((cf) => {
            const { label, key, rules = [], initialValue, required, ...restConfig } = cf;
            if (required) {
                rules.push({
                    required: true, message: `${label}`,
                });
            }
            return (
                <Form.Item label={label} key={`${key}-item`} {...formItemLayout}>
                    {getFieldDecorator(hasIndex ? `${key}-${initData.index}` : key, {
                            rules,
                            initialValue: initData[key] || initialValue,
                        })(renderComponent({ type: restConfig.type, configs: restConfig }))}
                </Form.Item>
            );
        });
    }
    return null;
};

const renderModalSelect = (layerConfig = {}, form, initialValue, key = '') => {
    const keyStr = `${key}`;
    const { modalArr = [] } = layerConfig;
    if (modalArr.length === 0) {
        return null;
    }
    const formItemKey = keyStr ? `modal-item-${keyStr}` : 'modal-item';
    const formNameKey = keyStr ? `modalName-${keyStr}` : 'modalName';
    return (
        <Form.Item
            key={formItemKey}
            label="modal"
            {...formItemLayout}
        >
            {form.getFieldDecorator(formNameKey, {
                rules: [{ required: true, 'message': 'modal' }],
                initialValue,
            })(
                <Select placeholder="modal">
                    {
                        modalArr.map(({ index, name }) => {
                            return (
                                <Option key={`modalName-${index}`} value={name}>{name}</Option>
                            )
                        })
                    }
                </Select>
            )}
        </Form.Item>
    );
};

const renderApiSelect = (layerConfig = {}, form, compData) => {
    const { initialValue, key = '', label = 'api', apiName } = compData;
    const keyStr = `${key}`;
    const { apiArr = [] } = layerConfig;
    if (apiArr.length === 0) {
        return null;
    }
    const formItemKey = apiName ? `api-item-${apiName}` : keyStr ? `api-item-${keyStr}` : 'modal-item';
    const formNameKey = apiName ? apiName : keyStr ? `api-${keyStr}` : 'api';
    return (
        <Form.Item
            key={formItemKey}
            label={label}
            {...formItemLayout}
        >
            {form.getFieldDecorator(formNameKey, {
                rules: [{ required: true, 'message': 'api' }],
                initialValue,
            })(
                <Select placeholder="api">
                    {
                        apiArr.map(({ index, requestApi }) => {
                            return (
                                <Option key={`api-${index}`} value={requestApi}>{requestApi}</Option>
                            )
                        })
                    }
                </Select>
            )}
        </Form.Item>
    );
};

export default renderComponent;

export {
    renderExpandBtn,
    renderModalSelect,
    renderApiSelect,
    renderConfigForm,
};