import React, { PureComponent } from 'react';
import { Icon, message } from 'antd';
import FormItemRender from './FormItemRender';
import FormItemEdit from '../../EditDrawer/FormItemEdit';
import { activeColor } from '../../../utils/Constants';
import initDropData from '../../../utils/DropUtil';
import './index.css';

const clsName = 'drop-container border-box';
const forbiddenDropType = ['Breadcrumb', 'Text', 'Tabs'];

class FormItemContainer extends PureComponent {
    constructor(props) {
        super(props);
        const dropConfig = this.getConfigFromProps();
        this.state = {
            dropComponent: dropConfig && dropConfig.type ? true : false, // 
            dropConfig: Object.assign({
                colSpan: props.colSpan,
                colIndex: props.colIndex,
                originSpan: props.originSpan,
                cellStyles: props.cellStyles || {},
            }, dropConfig || {}), // 
            visible: false,
        };
        this.btnIndex = 1;
        this.onUpdateConfig = this.onUpdateConfig.bind(this);
        this.onShowEditItem = this.onShowEditItem.bind(this);
        this.onEditDropContainer = this.onEditDropContainer.bind(this);
        this.onDrop = this.onDrop.bind(this);
        this.onDeleteCol = this.onDeleteCol.bind(this);
        this.onClose = this.onClose.bind(this);
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.colSpan !== this.props.colSpan || nextProps.colIndex !== this.props.colIndex) {
            const newdropConfig = Object.assign({},
                this.state.dropConfig, {
                    colSpan: nextProps.colSpan,
                    colIndex: nextProps.colIndex,
                    originSpan: nextProps.originSpan,
                    cellStyles: nextProps.cellStyles,
                });
            this.setState({
                dropConfig: newdropConfig,
            });
        }
    }
    getConfigFromProps() {
        const { configs = {} } = this.props;
        const { formItemArr = [] } = configs;
        return formItemArr.find((formItem) => {
            return formItem.colIndex === this.props.colIndex;
        });
    }
    onShowEditItem() {
        this.setState({
            visible: true,
        });
    }
    onDrop(event) {
        event.preventDefault();
        if (event.target.className === clsName) {
            this.resetBg();
            const data = JSON.parse(event.dataTransfer.getData('text/plain'));
            const { type } = data;
            if (~forbiddenDropType.indexOf(type)) {
                message.warn('!');
                return;
            }
            const { onUpdateConfigs, colIndex } = this.props;
            const dropConfig = initDropData(data, this, true);
            if (dropConfig) {
                this.setState({
                    dropConfig: { colIndex, ...dropConfig },
                    dropComponent: true,
                }, () => {
                    onUpdateConfigs(this.state.dropConfig);
                });
            }
        }
    }
    /**
     * @desc  
     * @param {object} e 
     */
    onDeleteCol(e) {
        e && e.stopPropagation();
        const { onUpdateConfigs, colIndex } = this.props;
        const { dropConfig } = this.state;
        this.setState({
            dropConfig: {
                colSpan: dropConfig.colSpan,
                originSpan: dropConfig.originSpan,
                colIndex,
            },
            dropComponent: false,
        });
        onUpdateConfigs({ colIndex, deleteFlag: true });
    }
    resetBg() {
        const dropDomArr = document.querySelectorAll('.form-container .drop-container');
        if (dropDomArr) {
            dropDomArr.forEach((item) => {
                item.style.background = '';
            });
        }
    }
    onDragenter(event) {
        if (event.target.className === clsName) {
            event.target.style.background = activeColor;
        }
    }
    onDragLeave(event) {
        if (event.target.className === clsName) {
            event.target.style.background = '';
        }
    }
    componentWillUnmount() {
        if (this.targetDom) {
            this.targetDom.removeEventListener('drop', this.onDrop);
            this.targetDom.removeEventListener('dragleave', this.onDragLeave);
            this.targetDom.removeEventListener("dragenter", this.onDragenter);
        }
    }
    componentDidMount() {
        if (!this.targetDom) {
            return;
        }
        this.targetDom.addEventListener("drop", this.onDrop, false);
        this.targetDom.addEventListener("dragleave", this.onDragLeave, false);
        this.targetDom.addEventListener("dragenter", this.onDragenter, false);
    }
    onUpdateConfig(config) {
        const { dropConfig } = this.state;
        let newdropConfig = null;
        // 
        if (config.deleteFlag) {
            newdropConfig = {
                colIndex: config.colIndex,
                colSpan: config.colSpan,
                originSpan: config.originSpan,
                cellStyles: config.cellStyles,
            }
        } else {
            const { colIndex, colSpan, originSpan, cellStyles, ...configs } = config;
            newdropConfig = Object.assign(dropConfig, { colIndex, colSpan, originSpan, cellStyles, configs });
        }
        this.setState({
            dropConfig: newdropConfig,
            dropComponent: !config.deleteFlag,
        }, () => {
            const { onUpdateConfigs } = this.props;
            onUpdateConfigs(newdropConfig);
            this.onClose();
        });
    }
    onEditDropContainer(e) {
        if (e.target.className === clsName) {
            e && e.stopPropagation();
            this.setState({
                visible: true,
            });
        }
    }
    onClose() {
        this.setState({
            visible: false,
        });
    }
    getContainerWidth(colSpan = 8) {
        return `${100 * colSpan / 24 }%`;
    }
    render() {
        const { dropComponent, dropConfig = {}, visible } = this.state;
        const { layoutColumn, onDeleteFormItemContainer, colIndex, configs = {}, preview } = this.props;
        const colIndexNum = + colIndex;
        const _colSpan = Object.hasOwnProperty.call(dropConfig, 'colSpan') ? 
            dropConfig.colSpan : (24 / layoutColumn);
        return (
            <div
                className={preview ? `preview-box ${clsName}` : clsName}
                style={{
                    width: this.getContainerWidth(_colSpan, layoutColumn),
                }}
                ref={(targetDom) => { this.targetDom = targetDom; }}
            >
                {
                  dropComponent || preview ? (
                        <FormItemRender
                            {...this.props}
                            formItemConfig={preview ? this.getConfigFromProps() : dropConfig}
                        />
                    ) : <Icon type="drag" />
                }
                {
                    !preview ? 
                        <Icon
                            type="bars"
                            className="icon-operation"
                            onClick={this.onShowEditItem}
                        /> : null
                }
                <FormItemEdit
                    visible={visible}
                    onDelete={this.onDeleteCol}
                    onUpdateConfigs={this.onUpdateConfig}
                    colSpanArr={configs.colSpanArr}
                    formItemArr={configs.formItemArr}
                    dropConfig={dropConfig}
                    dropComponent={dropComponent}
                    layoutColumn={layoutColumn}
                    onDeleteFormItemContainer={() => {
                        this.setState({
                            visible: false,
                        }, () => {
                            onDeleteFormItemContainer(colIndexNum);
                        });
                    }}
                    onClose={this.onClose}
                />
            </div>
        );
    }
}

export default FormItemContainer;