import React, { Component } from 'react';
import zhCN from 'antd/lib/locale-provider/zh_CN';
import { Layout, LocaleProvider } from 'antd';
import PageNameModal from './component/Modal/PageNameModal';
import PreviewPageModal from './component/Modal/PreviewPageModal';
import DragTabs from './component/DragTabs';
import { isLogin } from './utils/common';
import MainContainer from './component//Container/MainContainer';
import LoginModal from './component/Modal/LoginModal';

const { Sider, Content } = Layout;

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showLoginModal: false,
            showEditNameModal: false, // 
            showPreviewModal: false, // 
            pageName: '', // 
        };
        this.onSubmitPageName = this.onSubmitPageName.bind(this);
        this.onPreviewPage = this.onPreviewPage.bind(this);
        this.onShowLoginModal = this.onShowLoginModal.bind(this);
        this.onCloseLoginModal = this.onCloseLoginModal.bind(this);
        this.onClosePreviewModal = this.onClosePreviewModal.bind(this);
        this.onShowPageNameModal = this.onShowPageNameModal.bind(this);
        this.onClosePageNameModal = this.onClosePageNameModal.bind(this);
    }
    componentDidMount() {
        if (!isLogin()) {
            this.onShowLoginModal();
        }
    }
    onCloseLoginModal() {
        this.setState({
            showLoginModal: false,
        });
    }
    onShowLoginModal() {
        this.setState({
            showLoginModal: true,
        });
    }
    onShowPageNameModal() {
        this.setState({
            showEditNameModal: true,
        });
    }
    onSubmitPageName(pageName = '', projectDir) {
        this.setState({
            showEditNameModal: false,
            pageName,
            projectDir,
        }, () => {
            if (pageName && projectDir) {
                document.querySelector('#btn-code').click();
            }
        });
    }
    onClosePageNameModal() {
        this.setState({
            showEditNameModal: false,
        });
    }
    onClosePreviewModal() {
        this.setState({
            showPreviewModal: false,
        });
    }
    onPreviewPage(layoutConfig, layerConfig) {
        this.setState({
            showPreviewModal: true,
        });
        this.layoutConfig = layoutConfig;
        this.layerConfig = layerConfig;
	}
    render() {
        const {
            pageName,
            projectDir,
            showLoginModal,
            showPreviewModal,
            showEditNameModal,
        } = this.state;
        return (
            <div className="App">
                <LocaleProvider locale={zhCN}>
                    <Layout>
                        <Sider width="300">
                            <DragTabs />
                        </Sider>
                        <Layout>
                            <Content>
                                <MainContainer
                                    pageName={pageName}
                                    projectDir={projectDir}
                                    onShowLoginModal={this.onShowLoginModal}
                                    onShowPageNameModal={this.onShowPageNameModal}
                                    onPreviewPage={this.onPreviewPage}
                                />
                            </Content>
                        </Layout>
                        <PageNameModal
                            onOkModal={this.onSubmitPageName}
                            onCloseModal={this.onClosePageNameModal}
                            visible={showEditNameModal}
                        />
                        <PreviewPageModal
                            layoutConfig={this.layoutConfig}
                            layerConfig={this.layerConfig}
                            onCloseModal={this.onClosePreviewModal}
                            showModal={showPreviewModal}
                        />
                        <LoginModal
                            visible={showLoginModal}
                            onCloseLoginModal={this.onCloseLoginModal}
                        />
                    </Layout>
                </LocaleProvider>
            </div>
        );
    }
}

export default App;
