import React, { Component, Fragment } from 'react';
import { Form, Col } from 'antd';
import renderComponent from '../../../utils/RenderUtil';
import moment  from 'moment';

class FormItemRender extends Component {
    getInitialValue(formItemConfig = {}) {
        const { type = 'Input', initialValue } = formItemConfig;
        if (type === 'DatePicker' || type === 'RangePicker') {
            return initialValue && moment(initialValue, 'YYYY-MM-DD');
        }
        return formItemConfig.initialValue || '';
    }

	render() {
        const { formItemConfig, form, context, preview = false } =this.props;
        const { getFieldDecorator } = form;
        const { configs } = formItemConfig;
        const { colSpan, label, name, rules = [], cellStyles = {}, required, hasLinkage } = configs;
        const { type, textAlign } = formItemConfig;
        const md = colSpan || 8;
        if (!name && type !== 'Button') {
            return null;
        }
        // 
        if (hasLinkage === '1') {
            const { linkName, condition } = configs;
            if (form.getFieldValue(linkName) !== condition) {
                return null;
            }
        }
        if (required) {
            rules.push({
                required: true, message: `${label}`,
            });
        }
        const styleObj = type === 'Button' ? Object.assign({}, cellStyles, { textAlign }) : cellStyles;
		return (
			<Fragment>
				<Col
			        span={colSpan}
                    md={md} sm={24}
			    >
                    {
                        type !== 'Button' ?
                            (
                                <Form.Item label={label} style={styleObj}>
                                    {getFieldDecorator(name, {
                                        rules: rules || [],
                                        initialValue: this.getInitialValue(configs),
                                    })(
                                        renderComponent(formItemConfig, preview, context)
                                    )}
                                </Form.Item>
                            ) : (
                                <Form.Item style={styleObj}>
                                    {
                                        renderComponent(formItemConfig, preview, context)
                                    }
                                </Form.Item>
                            )
                    }
			      
			    </Col>
		    </Fragment>
	    );
	}
}

export default FormItemRender;