import React, { PureComponent } from 'react';
import { Divider, Icon } from 'antd';
import './index.css';

class LineContainer extends PureComponent {
    constructor(props) {
        super(props);
        this.onDelete = this.onDelete.bind(this);
    }
    onDelete() {
        const { onUpdateLayoutConfig } = this.props;
        onUpdateLayoutConfig({ deleteFlag: true });
    }
    render() {
        return (
            <div className="fulu-cell-box line-box">
                <Divider />
                <Icon type="close" className="delete-line" onClick={this.onDelete} />
            </div>
        );
    }
}

export default LineContainer;