import React, { PureComponent, Fragment } from 'react';
import { Modal, message, Spin } from 'antd';
import renderComponent from '../../../utils/RenderUtil';
import { getFormApi } from '../../../utils/FormUtil';
import { MODE_EDIT, FORM_CONTAINER, FORM_SAVE, CODE_SUCCESS } from '../../../utils/Constants';
import './index.css';

class PreviewModal extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
        };
        this.onCancel = this.onCancel.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.onAfterClose = this.onAfterClose.bind(this);
        this.onFormRenderCallBack = this.onFormRenderCallBack.bind(this);
        this.initEdit = false;
    }
    componentDidUpdate() {
        const { mode, visible, recordData, api, execFetchApi, modalLayoutConfig } = this.props;
        // 
        if (visible && mode === MODE_EDIT && !this.initEdit) {
            if (api) { // 
                execFetchApi(api, recordData);
            } else if (recordData) { // 
                const formObj = modalLayoutConfig.find(({ type, configs }) => {
                    return type === FORM_CONTAINER && configs.formType === FORM_SAVE;
                });
                if (formObj && Array.isArray(formObj.configs.formItemArr)) {
                    const formData = formObj.configs.formItemArr.reduce((obj, item) => {
                        const { type, name } = item;
                        obj[name] = recordData[name];
                        return obj;
                    }, {});
                    if (this.form) {
                        this.form.setFieldsValue(formData);
                    }
                    this.formData = formData;
                } else {
                    message.warn('');
                }
            }
            this.initEdit = true;
        }
    }
    onCancel() {
        const { loading } = this.state;
        if (loading) {
            return;
        }
        const { modalName, onCancelModal } = this.props;
        onCancelModal(modalName);
    }
    renderContent(layoutConfig = []) {
        const { mode } = this.props;
        return layoutConfig.map((item, i) => {
            return (
                <Fragment key={`preview-${i}`}>
                    {renderComponent(item, {
                        preview: true,
                        context: this,
                        mode,
                        onFormRenderCallBack: this.onFormRenderCallBack })}
                </Fragment>
            );
        });
    }
    onSubmit() {
        const { loading } = this.state;
        if (loading) {
            return;
        }
        const { onOkModal, modalName, mode, modalLayoutConfig, layerConfig, recordData } = this.props;
        if(this.form) {
            this.form.validateFields((err, values) => {
                if (!err) {
                    if (Array.isArray(modalLayoutConfig)) {
                        const formObj = modalLayoutConfig.find(({ type, configs }) => {
                            return type === FORM_CONTAINER && configs.formType;
                        });
                        if (formObj) {
                            const api = getFormApi(mode, formObj.configs); // 
                            const { apiArr } = layerConfig;
                            const apiObj = apiArr.find(({ requestApi }) => {
                                return requestApi === api;
                            });
                            if (apiObj && apiObj.requestParams) { // 
                                const paramsArr = apiObj.requestParams.split(',');
                                paramsArr.forEach((paramKey) => {
                                    if (Object.hasOwnProperty.call(recordData, paramKey) && !Object.hasOwnProperty.call(values, paramKey)) {
                                        values[paramKey] = recordData[paramKey];
                                    }
                                });
                            }
                            if (api) {
                                this.setState({
                                    loading: true,
                                });
                                onOkModal(modalName, api, values).then((res) => {
                                    const { data } = res;
                                    const { code } = data || {};
                                    this.setState({
                                        loading: false,
                                    });
                                    if (code === CODE_SUCCESS) {
                                        message.success('');
                                        this.onCancel();
                                    }
                                }).catch(() => {
                                    this.setState({
                                        loading: false,
                                    });
                                });
                            } else {
                                message.warn('formapi');
                            }
                        } else {
                            message.warn('form');
                        }
                    } else {
                        message.warn('form');
                    }
                }
            });
        } else {
            message.warn('form');
        }
    }
    onFormRenderCallBack(form) {
        this.form = form;
        if (this.formData) {
            this.form.setFieldsValue(this.formData);
        }
    }
    onAfterClose() {
        this.initEdit = false;
        this.formData = null;
        if (this.form) {
            this.form.resetFields();
        }

    }
    render() {
        const { title, visible, modalLayoutConfig, mode } = this.props;
        const { loading } = this.state;
        return (
            <Modal
                title={mode === MODE_EDIT ? '' : title}
                visible={visible}
                onCancel={this.onCancel}
                onOk={this.onSubmit}
                afterClose={this.onAfterClose}
                maskClosable={false}
            >
                <Spin spinning={loading}>
                    {this.renderContent(modalLayoutConfig)}
                </Spin>
            </Modal>
        );
    }
}

export default PreviewModal;