import React, { PureComponent, Fragment } from 'react';
import { Button, Card, Drawer, Divider, Collapse, Form, Input, Icon, Select } from 'antd';
import formItemLayout from '../../../configs/layout';
import getTableConfig from '../../../utils/TableUtil';
import { getFormDataByIndex } from '../../../utils/FormUtil';
import { renderApiSelect, renderModalSelect } from '../../../utils/RenderUtil';
import { TRUE, FALSE } from '../../../utils/Constants';
import './index.css';

const Panel = Collapse.Panel;
const Option = Select.Option;

const fLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 10 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 14 },
    },
};

class TableEdit extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            configs: props.configs,
        };
        this.setColumnMaxIndex(props);
        this.onSubmit = this.onSubmit.bind(this);
        this.onAddColumn = this.onAddColumn.bind(this);
        this.onDeleteColumn = this.onDeleteColumn.bind(this);
        this.onAddOperation = this.onAddOperation.bind(this);
        this.onDeleteOperation = this.onDeleteOperation.bind(this);
        this.renderOperationBtn = this.renderOperationBtn.bind(this);
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.visible !== this.props.visible && nextProps.visible) {
            this.setState({
                configs: nextProps.configs,
            });
            this.setColumnMaxIndex(nextProps);
            this.setOperationMaxIndex(nextProps);
        }
    }
    setColumnMaxIndex({ configs }) {
        const { columns = [] } = configs;
        this.columnMaxIndex = columns.length || 0;
    }
    setOperationMaxIndex({ configs }) {
        const { operationArr = [] } = configs;
        this.operationMaxIndex = operationArr.length || 0;
    }
    addColumnMaxIndex() {
        this.columnMaxIndex += 1;
    }
    addOperationMaxIndex() {
        this.operationMaxIndex += 1;
    }
    onAddOperation() {
        const { configs } = this.state;
        const { operationArr = [] } = configs;
        const newOperationArr = operationArr.slice();
        const opreationIndex = this.operationMaxIndex;
        newOperationArr.push({
            opText: "",
            index: opreationIndex,
        });
        this.setState({
            configs: this.getNewConfigs('operationArr', newOperationArr),
        });
        this.addOperationMaxIndex();
    }
    onAddColumn() {
        const { configs } = this.state;
        const { columns } = configs;
        const newColumns = columns.slice();
        const columnIndex = this.columnMaxIndex;
        newColumns.push({
            title: `column${columnIndex}`,
            dataIndex: `dataIndex${columnIndex}`,
            index: columnIndex,
        });
        this.setState({
            configs: this.getNewConfigs('columns', newColumns),
        });
        this.addColumnMaxIndex();
    }
    onDeleteOperation(index) {
        const { configs } = this.state;
        const { operationArr } = configs;
        const newOperationArr = operationArr.reduce((arr, item) => {
            if (item.index !== index) {
                arr.push(Object.assign({}, item));
            }
            return arr;
        }, []);
        const newConfigs = Object.assign({}, configs);
        newConfigs.operationArr = newOperationArr;
        this.setState({
            configs: this.getNewConfigs('operationArr', newOperationArr),
        });
    }
    onDeleteColumn(columnIndex) {
        const { configs } = this.state;
        const { columns } = configs;
        const newColumns = columns.reduce((arr, item) => {
            if (item.index !== columnIndex) {
                arr.push(Object.assign({}, item));
            }
            return arr;
        }, []);
        const newConfigs = Object.assign({}, configs);
        newConfigs.columns = newColumns;
        this.setState({
            configs: this.getNewConfigs('columns', newColumns),
        });
    }
    getNewConfigs(k, v) {
        const { configs } = this.state;
        const newConfigs = Object.assign({}, configs);
        newConfigs[k] = v;
        return newConfigs;
    }
    onSubmit(e) {
        e.preventDefault();
        const { context, form, onUpdateLayoutConfig } = this.props;
        form.validateFields((err, values) => {
            if (!err) {
                const { configs } = this.state;
                const newOperationArr = [];
                // 
                if (values.hasOperation === TRUE) {
                    const { operationArr = [] } = configs;
                    const opArr = ['opText', 'opType', 'modalName', 'modalData', 'api'];
                    for (let i = 0; i< operationArr.length; i++) {
                        const { index } = operationArr[i];
                        newOperationArr.push(Object.assign(operationArr[i], getFormDataByIndex(values, opArr, index)));
                    }
                }
                const newConfigs = getTableConfig(values, configs, context);
                newConfigs.operationArr = newOperationArr;
                delete newConfigs.pageSize;
                delete newConfigs.pageSizeOptions;
                delete newConfigs.type;
                delete newConfigs.columnTitle;
                onUpdateLayoutConfig(newConfigs);
            }
        });
    }
    renderPanelHeader({ title, index }) {
        return (
            <div className="panel-header">
                <span className="title">{title}</span>
                <Icon
                    type="close"
                    className="btn-delete"
                    onClick={() => {
                        this.onDeleteColumn(index);
                    }}
                />
            </div>
        );
    }
    renderOperationHeader({ opText, index }) {
        return (
            <div className="panel-header">
                <span className="title">{opText}</span>
                <Icon
                    type="close"
                    className="btn-delete"
                    onClick={() => {
                        this.onDeleteOperation(index);
                    }}
                />
            </div>
        );
    }
    renderCollapse() {
        const { form } = this.props;
        const { configs } = this.state;
        const { columns = [] } = configs;
        const { getFieldDecorator } = form;
        return (
            <Collapse>
                {
                    columns.map((item, i) => {
                        const { title, index, dataIndex, width, sortIndex, align = 'left', fixed = false } = item;
                        if (title === '' && i === columns.length -1) {
                            return null;
                        }
                        return (
                            <Panel header={this.renderPanelHeader(item)} key={`${title}-${index}`}>
                                <Form.Item label="title" {...formItemLayout}>
                                    {getFieldDecorator(`title-${index}`, { 
                                        rules: [{ required: true, message: 'title!' }],
                                        initialValue: title,
                                    })(
                                        <Input />
                                    )}
                                </Form.Item>
                                <Form.Item label="dataIndex" {...formItemLayout}>
                                    {getFieldDecorator(`dataIndex-${index}`, { 
                                        rules: [{ required: true, message: 'dataIndex!' }],
                                        initialValue: dataIndex,
                                    })(
                                        <Input />
                                    )}
                                </Form.Item>
                                <Form.Item label="width" {...formItemLayout}>
                                    {getFieldDecorator(`width-${index}`, {
                                        initialValue: width || '',
                                    }, {
                                        rules: [{ pattern: /^\d+(%)?$/, message: '!' }]
                                    })(
                                        <Input />
                                    )}
                                </Form.Item>
                                {
                                    i === 0 ? (
                                        <Form.Item label="" {...formItemLayout}>
                                            {getFieldDecorator(`sortIndex-${index}`, {
                                                initialValue: sortIndex,
                                            })(
                                                <Select placeholder="">
                                                    <Option value="0">false</Option>
                                                    <Option value="1">true</Option>
                                                </Select>
                                            )}
                                        </Form.Item>
                                    ): null
                                }
                                <Form.Item label="align" {...formItemLayout}>
                                    {getFieldDecorator(`align-${index}`, { 
                                        initialValue: align,
                                    })(
                                        <Select>
                                            <Option value={'left'}>left</Option>
                                            <Option value={'right'}>right</Option>
                                            <Option value={'center'}>center</Option>
                                        </Select>
                                    )}
                                </Form.Item>
                                <Form.Item label="fixed" {...formItemLayout}>
                                    {getFieldDecorator(`fixed-${index}`, { 
                                        initialValue: fixed ? "1" : "0",
                                    })(
                                        <Select>
                                            <Option value="0">false</Option>
                                            <Option value="1">true</Option>
                                        </Select>
                                    )}
                                </Form.Item>
                            </Panel>
                        );
                    })
                }
            </Collapse>
        );
    }
    renderOperationBtn(btnConfig) {
        const { form, layerConfig } = this.props;
        const { getFieldDecorator } = form;
        const { index, opText, opType, modalName, modalData, api } = btnConfig;
        const _opType = form.getFieldValue(`opType-${index}`);
        return (
            <Panel header={this.renderOperationHeader(btnConfig)} key={`op-${index}`}>
                <Form.Item label="" {...formItemLayout}>
                    {getFieldDecorator(`opText-${index}`, { 
                        initialValue: opText,
                    })(
                        <Input />
                    )}
                </Form.Item>
                <Form.Item label="" {...formItemLayout}>
                    {getFieldDecorator(`opType-${index}`, { 
                        initialValue: opType,
                    })(
                        <Select>
                            <Option value="edit">()</Option>
                            <Option value="view">()</Option>
                            <Option value="delete"></Option>
                            <Option value="skip"></Option>
                        </Select>
                    )}
                </Form.Item>
                {
                    _opType === 'edit' || _opType === 'view' ? renderModalSelect(layerConfig, form, modalName, index) : null
                }
                {
                    _opType === 'edit' || _opType === 'view' ? (
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator(`modalData-${index}`, {
                                initialValue: modalData,
                            })(
                                <Select>
                                    <Option value="table"></Option>
                                    <Option value="api"></Option>
                                </Select>
                            )}
                        </Form.Item>
                    ) : null
                }
                {
                    form.getFieldValue(`modalData-${index}`) === 'api' ? renderApiSelect(layerConfig, form, { initialValue: api, key: index }) : null
                }
            </Panel>
        )
    }
    renderTableProps() {
        const { form } = this.props;
        const { configs } = this.state;
        const { getFieldDecorator } = form;
        const {  operationArr = [], pagination, rowSelection, hasOperation = FALSE, mockData = 0, rowKey } = configs;
        const { pageSize = 10, pageSizeOptions = '10,20,30' } = pagination || {};
        const { type = 'checkbox', columnTitle = '' } = rowSelection || {};
        const hasPagination = Object.keys(pagination || {}).length > 0 ? TRUE : FALSE;
        const hasRowSelection = Object.keys(rowSelection || {}).length > 0 ? TRUE : FALSE;
        return (
            <Fragment>
                <Form.Item label="" {...formItemLayout}>
                    {getFieldDecorator("hasOperation", { 
                        initialValue: hasOperation,
                    })(
                        <Select>
                            <Option value={FALSE}></Option>
                            <Option value={TRUE}></Option>
                        </Select>
                    )}
                </Form.Item>
                {
                    form.getFieldValue('hasOperation') === TRUE ? // 
                        [
                            <Collapse key="op-collapse">
                                {
                                    operationArr.map(this.renderOperationBtn)
                                }
                            </Collapse>,
                            <Fragment key="op-collapse-btns">
                                {
                                    form.getFieldValue('hasOperation') === TRUE ?
                                        (<div className="btn-add-box">
                                            <Button
                                                type="dashed"
                                                size="large"
                                                onClick={this.onAddOperation}
                                            >
                                                
                                            </Button>
                                        </div>) : null
                                }
                            </Fragment>
                        ] : null
                }
                <Form.Item label="pagination" {...formItemLayout}>
                    {getFieldDecorator("pagination", { 
                        initialValue: hasPagination,
                    })(
                        <Select>
                            <Option value="0">false</Option>
                            <Option value="1">true</Option>
                        </Select>
                    )}
                </Form.Item>
                <Form.Item label="rowKey" {...formItemLayout}>
                    {getFieldDecorator("rowKey", { 
                        initialValue: rowKey,
                    })(
                        <Input />
                    )}
                </Form.Item>
                {
                    form.getFieldValue('pagination') === '1' ?
                    <Card title="pagination">
                        <Form.Item label="pageSize" {...formItemLayout}>
                            {getFieldDecorator("pageSize", { 
                                initialValue: pageSize,
                            })(
                                <Input />
                            )}
                        </Form.Item>
                        <Form.Item label="pageSizeOptions" {...fLayout}>
                            {getFieldDecorator("pageSizeOptions", { 
                                initialValue: Array.isArray(pageSizeOptions)
                                    ? pageSizeOptions.join(',') : pageSizeOptions,
                            })(
                                <Select>
                                    <Option value="10,20,30">10,20,30</Option>
                                    <Option value="10,20,30,40">10,20,30,40</Option>
                                    <Option value="20,30,40">20,30,40</Option>
                                </Select>
                            )}
                        </Form.Item>
                    </Card> : null
                }
                <Form.Item label="rowSelection" {...formItemLayout}>
                    {getFieldDecorator("rowSelection", { 
                        initialValue: hasRowSelection,
                    })(
                        <Select>
                            <Option value="0">false</Option>
                            <Option value="1">true</Option>
                        </Select>
                    )}
                </Form.Item>
                {
                    form.getFieldValue('rowSelection') === '1' ?
                    <Card title="rowSelection">
                        <Form.Item label="type" {...formItemLayout}>
                            {getFieldDecorator("type", { 
                                initialValue: type,
                            })(
                                <Select>
                                    <Option value="checkbox">checkbox</Option>
                                    <Option value="radio">radio</Option>
                                </Select>
                            )}
                        </Form.Item>
                        {
                            form.getFieldValue('type') === 'radio' ?
                                <Form.Item label="columnTitle" {...formItemLayout}>
                                    {getFieldDecorator("columnTitle", { 
                                        initialValue: columnTitle,
                                    })(
                                        <Input />
                                    )}
                                </Form.Item> : null
                        }
                    </Card> : null
                }
                {/* <Form.Item label="" {...formItemLayout}>
                    {getFieldDecorator("mockData", {
                        initialValue: mockData,
                    })(
                        <Input placeholder="" />
                    )}
                </Form.Item> */}
            </Fragment>
        );
    }
    render() {
        const { visible, onClose, onDeleteContainer, form } = this.props;
        const { getFieldDecorator } = form;
        const { configs } = this.state;
        const {  dataSrouce } = configs;
        return (
            <Drawer
                title=""
                maskClosable={false}
                width={420}
                onClose={onClose}
                visible={visible}
            >
                <Form onSubmit={this.onSubmit}>
                    <Divider></Divider>
                    {this.renderCollapse()}
                    <div className="btn-add-box">
                        <Button
                            type="dashed"
                            size="large"
                            onClick={this.onAddColumn}
                        >
                            
                        </Button>
                    </div>
                    <Divider></Divider>
                    {this.renderTableProps()}
                    <Divider></Divider>
                    <Form.Item label="dataSrouce" {...formItemLayout}>
                        {getFieldDecorator("modelKey", {
                            initialValue: dataSrouce,
                        })(
                            <Input placeholder="model key" />
                        )}
                    </Form.Item>
                    <Form.Item style={{ marginTop: '20px', textAlign: 'center' }}>
                        <Button
                            type="primary"
                            htmlType="submit"
                            style={{ marginRight: '10px' }}
                        >
                            
                        </Button>
                        <Button onClick={onDeleteContainer}></Button>
                    </Form.Item>
                </Form>
            </Drawer>
        );
    }
}

export default Form.create()(TableEdit);