import React, { Component, Fragment } from 'react';
import { Button, Divider, message, Form, Input, Select, Drawer } from 'antd';
import { renderApiSelect } from '../../../utils/RenderUtil';
import formItemLayout from '../../../configs/layout';
import './index.css';

const Option = Select.Option;

class FormEdit extends Component {
    constructor(props) {
        super(props);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }
    handleChange(value) {
        const colSpanValues = {};
        const val = 24 / value;
        for(let i = 0; i < value; i++) {
            colSpanValues[`colSpan-${i}`] = val;
        }
        setTimeout(() => {
            this.props.form.setFieldsValue(colSpanValues);
        }, 0);
    }
    handleSubmit(e) {
        e.preventDefault();
        const { form, onClose } = this.props;
        form.validateFields((err, values) => {
            if (!err) {
                const lcNum = +values.layoutColumn;
                let colSpanTotal = 0;
                const configs = {
                    colSpanArr: [],
                };
                for (let i = 0; i < lcNum; i++) {
                    const colSpan = +values[`colSpan-${i}`];
                    delete values[`colSpan-${i}`];
                    colSpanTotal += colSpan;
                    configs.colSpanArr.push(colSpan);
                }
                if (colSpanTotal > 24) {
                    message.error('24!');
                    return;
                }
                onClose(Object.assign(configs, values));
            }
        });
    }
    renderColSpanInputs() {
        const { form } = this.props;
        const { getFieldDecorator } = form;
        const layoutColumn = +form.getFieldValue('layoutColumn');
        const componentArr = [];
        for (let i = 0; i < layoutColumn; i++) {
            componentArr.push(
                <Form.Item
                    key={`formitem-${i}`}
                    label={`${i+1}`}
                    {...formItemLayout}
                >
                    {getFieldDecorator(`colSpan-${i}`, {
                        initialValue: 24 / layoutColumn,
                        rules: [
                            { required: true, message: '!' },
                            { pattern: /^[1-9](\d)?$/, message: '' }
                        ],
                    })(
                        <Input />
                    )}
                </Form.Item>
            );
        }
        return componentArr;
    }
    renderApiFormItem() {
        const { form, layerConfig } = this.props;
        if (form.getFieldValue('formType') === 'query') {
            return renderApiSelect(layerConfig, form, { label: 'api', apiName: 'queryApi' });
        }
        return (
            <Fragment>
                {renderApiSelect(layerConfig, form, { label: 'api', apiName: 'addApi' })}
                {renderApiSelect(layerConfig, form, { label: 'api', apiName: 'editApi' })}
            </Fragment>
        );
    }
    render() {
        const { visible, form, onClose, onDeleteContainer } = this.props;
        const { getFieldDecorator } = form;
        return (
            <Drawer
                visible={visible}
                maskClosable={false}
                width={420}
                title=""
                onClose={() => {
                    onClose(0);
                }}
            >
                <Form
                    onSubmit={this.handleSubmit}
                    style={{ padding: '0 20px' }}
                >
                    <Form.Item
                        label=""
                        {...formItemLayout}
                    >
                        {getFieldDecorator('layoutColumn', {
                            initialValue: 3,
                            rules: [
                                { required: true, message: '!' },
                            ],
                        })(
                            <Select onChange={this.handleChange}>
                                <Option value="1">1</Option>
                                <Option value="2">2</Option>
                                <Option value="3">3</Option>
                                <Option value="4">4</Option>
                            </Select>
                        )}
                    </Form.Item>
                    <Divider />
                    {this.renderColSpanInputs()}
                    <Divider />
                    <Form.Item
                        label=""
                        {...formItemLayout}
                    >
                        {getFieldDecorator('formType', {
                            initialValue: 'query',
                            rules: [
                                { required: true, message: '!' },
                            ],
                        })(
                            <Select>
                                <Option value="query"></Option>
                                <Option value="save">/</Option>
                            </Select>
                        )}
                    </Form.Item>
                    {
                        this.renderApiFormItem()
                    }
                    <div className="btn-box">
                        <Button
                            type="primary"
                            htmlType="submit"
                            style={{ marginRight: '10px' }}
                        >
                            
                        </Button>
                        <Button onClick={onDeleteContainer}></Button>
                    </div>
                </Form>
            </Drawer>
        );
    }
}

export default Form.create()(FormEdit);