import React, { Component } from 'react';
import { Tabs } from 'antd';
import DragTabFactory from '../Core/DragTabFactory';
import './index.css';

const TabPane = Tabs.TabPane;

class DragTabs extends Component {
	componentDidMount() {
        /*  */
         document.addEventListener("dragover", function( event ) {
             // drop
             event.preventDefault();
         }, false);

        /* drag */
         document.addEventListener("drag", function( event ) {

        }, false);

        document.addEventListener("dragstart", function( event ) {
          // 
              event.target.style.opacity = .5;
        }, false);

        document.addEventListener("dragend", function( event ) {
            // 
            event.target.style.opacity = "";
            document.querySelectorAll('.drop-zone').forEach((t) => {
              	t.style.background = "#fff";
            })
        }, false);

        document.addEventListener("dragenter", function( event ) {
          // 
          if ( event.target.className === "drop-zone" ) {
              event.target.style.background = "#96cdf9";
          }
        }, false);

        document.addEventListener("dragleave", function( event ) {
          // 
          if ( event.target.className === "drop-zone" ) {
              event.target.style.background = "";
          }
        }, false);
    }
	render() {
		return (
			<Tabs defaultActiveKey="1" className="aside-tabs">
			    <TabPane tab="" key="layout">
                    <DragTabFactory tabType="layout" />
			    </TabPane>
			    <TabPane tab="" key="component">
                    <DragTabFactory />
			    </TabPane>
                <TabPane tab="" key="template">
                    <DragTabFactory tabType="template" />
			    </TabPane>
		  	</Tabs>
		);
	}
}

export default DragTabs;
