import React, { PureComponent } from 'react';
import { Icon, Table } from 'antd';
import TableEdit from '../../EditDrawer/TableEdit';

import './index.css';

class TableContainer extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            visible: false,
        };
        this.onClose = this.onClose.bind(this);
        this.onShowEditTable = this.onShowEditTable.bind(this);
        this.onUpdateConfigs = this.onUpdateConfigs.bind(this);
        this.onDeleteContainer = this.onDeleteContainer.bind(this);
        this.onPageIndexChange = this.onPageIndexChange.bind(this);
        this.onShowSizeChange = this.onShowSizeChange.bind(this);
    }
    onShowEditTable() {
        this.setState({
            visible: true,
        });
    }
    onClose(e) {
        e.stopPropagation();
        this.setState({
            visible: false,
        });
    }
    onUpdateConfigs(configs) {
        const { layoutIndex, onUpdateLayoutConfig } = this.props;
        onUpdateLayoutConfig({layoutIndex, configs});
        this.setState({
            visible: false,
        });
    }
    onDeleteContainer() {
        const { layoutIndex, onUpdateLayoutConfig } = this.props;
        onUpdateLayoutConfig({ layoutIndex, deleteFlag: true });
    }
    getMockDataSource(columns, dataSize) {
        const ds = [];
        for (let i = 0; i < dataSize; i++) {
            const dsItem = {};
            columns.forEach((item) => {
                Object.keys(item).forEach((k) => {
                    dsItem[item.dataIndex] = `${item.dataIndex}-${i}`;
                });
            });
            ds.push(dsItem);
        }
        return ds;
    }
    getModelKey() {
        const { configs = {} } = this.props;
        const { modelKey } = configs;
        return modelKey;
    }
    onPageIndexChange(page, pageSize) {
        this.props.onPageIndexChange(page, pageSize, this.getModelKey());
    }
    onShowSizeChange(current, size) {
        this.props.onShowSizeChange(current, size, this.getModelKey());
    }
    render() {
        const { visible } = this.state;
        const { configs = {}, apiData = {}, currentPage, pageSize, context, layerConfig, preview = false } = this.props;
        const { pagination = false, rowSelection = null, columns = [], modelKey, mockData, rowKey } = configs;
        const copyColumns = columns.slice();
        const mockDataNum = (/^\d+$/).test(mockData) ? +mockData : 0;
        // api
        const hasApiDataSource = modelKey && Object.hasOwnProperty.call(apiData, modelKey);
        const dataSource = hasApiDataSource ? apiData[modelKey] : this.getMockDataSource(columns, mockDataNum);
        // 
        if (pagination) {
            pagination.showQuickJumper = true;
            pagination.showSizeChanger = true;
            pagination.current = currentPage;
            pagination.pageSize = pageSize;
            pagination.total = hasApiDataSource ? apiData[`${modelKey}Total`] : mockDataNum;
            pagination.onChange = this.onPageIndexChange;
            pagination.onShowSizeChange = this.onShowSizeChange;
        }
        return (
            <div className={`table-container container ${preview ? 'preview-box' : ''}`}>
                <Table
                    columns={copyColumns}
                    bordered
                    dataSource={dataSource}
                    pagination={pagination}
                    rowSelection={rowSelection}
                    rowKey={rowKey}
                />
                <TableEdit
                    visible={visible}
                    layerConfig={layerConfig}
                    configs={configs}
                    context={context}
                    onClose={this.onClose}
                    onDeleteContainer={this.onDeleteContainer}
                    onUpdateLayoutConfig={this.onUpdateConfigs}
                />
                {
                    !preview ? 
                        <Icon type="form" className="icon-operation" onClick={this.onShowEditTable} /> : null
                }
            </div>
        );
    }
}

export default TableContainer;