import React, { PureComponent } from 'react';
import { Icon, Button } from 'antd';
import DropContainer from '../DropContainer';
import BoxEdit from '../../EditDrawer/BoxEdit';
import './index.css';

class BoxContainer extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            visible: false,
            configs: {
                cellsArr: [{
                    dropIndex: 0,
                }],
                layoutColumn: 1, // 
            },
        };
        this.onAddRowCell = this.onAddRowCell.bind(this);
        this.onToggleEdit = this.onToggleEdit.bind(this);
        this.onDeleteContainer = this.onDeleteContainer.bind(this);
        this.onUpdateConfigs = this.onUpdateConfigs.bind(this);
    }
    onToggleEdit() {
        this.setState({
            visible: !this.state.visible,
        });
    }
    onDeleteContainer() {
        const { onUpdateLayoutConfig, layoutIndex } = this.props;
        onUpdateLayoutConfig({ layoutIndex, deleteFlag: true });
        this.onToggleEdit();
    }
    onUpdateConfigs({ deleteFlag = false, ...values }) {
        const { configs } = this.state;
        const { onUpdateLayoutConfig, layoutIndex, layoutColumn } = this.props;
        const newConfigs = Object.assign({ ...configs }, layoutColumn);
        const newCellsArr = newConfigs.cellsArr.reduce((arr, item) => {
            // 
            if (deleteFlag) {
                if (values.dropIndex !== item.dropIndex) {
                    arr.push(Object.assign({}, item));
                } else {
                    arr.push({
                        dropIndex: values.dropIndex,
                    });
                }
            } else {
                if (values.dropIndex === item.dropIndex) {
                    arr.push(Object.assign({}, item, values));
                } else {
                    arr.push(Object.assign({}, item));
                }
            }
            return arr;
        }, []);
        newConfigs.cellsArr = newCellsArr;
        this.setState({
            configs: newConfigs,
        }, () => {
            onUpdateLayoutConfig({ layoutIndex, configs: newConfigs });
        });
    }
    onAddRowCell() {
        const { onUpdateLayoutConfig, layoutIndex } = this.props;
        const { configs } = this.state;
        const { cellsArr, layoutColumn } = configs;
        const newCellsArr = cellsArr.concat([{
            dropIndex: cellsArr.length,
        }]);
        const newConfigs = {
            layoutColumn,
            cellsArr: newCellsArr,
        };
        const newState = {
            configs: newConfigs,
        };
        this.setState({
            ...newState,
        }, () => {
            onUpdateLayoutConfig({ layoutIndex, configs: newConfigs });
        });
    }
    rendeCell(layoutColumn) {
        if (!layoutColumn) {
            return null;
        }
        const { preview, context = this, apiData, execFetchApi, onShowLoginModal } = this.props;
        const componentArr = [];
        const width = `${100 / layoutColumn}%`;
        const { configs } = preview ? this.props : this.state;
        const { cellsArr } = configs;
        const cellNum = cellsArr.length;
        const { layerConfig } = this.props;
        const previewProps = {
            preview,
        };
        for (let i = 0; i < cellNum; i++) {
            previewProps.dropConfig = cellsArr[i];
            componentArr.push((
               <div className="cell border-box" key={`cell-${layoutColumn}-${i}`} style={{ width }}>
                    <DropContainer
                        dropIndex={i}
                        apiData={apiData}
                        execFetchApi={execFetchApi}
                        layerConfig={layerConfig}
                        {...previewProps}
                        onShowLoginModal={onShowLoginModal}
                        context={context}
                        onUpdateLayoutConfig={this.onUpdateConfigs}
                    />
               </div>
            ));
        }
        return componentArr;
    }
    render() {
        const { visible } = this.state;
        const { onUpdateLayoutConfig, layoutIndex, configs = {}, preview } = this.props;
        const { layoutColumn = 1, boxStyles } = configs;
        return (
            <div className={`fulu-cell-box ${ preview ? 'preview-mode' : 'edit-mode'}`} style={boxStyles}>
                <div className="clear-box">
                    {this.rendeCell(+layoutColumn)}
                </div>
                <BoxEdit
                    visible={visible}
                    configs={configs}
                    onClose={(configs) => {
                        if (configs) {
                            const layoutConfig = {
                                layoutIndex,
                            };
                            const { layoutColumn } = configs;
                            const { cellsArr } = this.state.configs;
                            const newCellsArr = cellsArr.slice(0, layoutColumn);
                            for (let i = 0; i < layoutColumn; i++) {
                                if (newCellsArr.length <= i) {
                                    newCellsArr.push({
                                        dropIndex: i
                                    });
                                }
                            }
                            const newConfigs = {
                                cellsArr: newCellsArr,
                                layoutColumn,
                                boxStyles: configs.boxStyles,
                            };
                            layoutConfig.configs = newConfigs;
                            this.setState({
                                configs: newConfigs,
                            });
                            onUpdateLayoutConfig(layoutConfig);
                        }
                        this.onToggleEdit();
                    }}
                    onDeleteContainer={this.onDeleteContainer}
                />
                {
                    !preview ? (
                        <Icon type="form" className="icon-operation" onClick={this.onToggleEdit} />
                    ) : null
                }
                {
                    !preview ? (
                        <div className="btn-add-box">
                            <Button onClick={this.onAddRowCell}>
                                
                            </Button>
                        </div>
                    ) : null
                }
            </div>
        );
    }
}

export default BoxContainer;